/**
* 配置->审核历史版本
* @module 配置中心
* @desc 审批历史版本
* @author 乔亚军
* @date 2019-6-26
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center append-to-body
                   width="750px">
            <div class="wrop">
               <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                   border size="mini" >
                    <el-table-column align="center" label="审批流程名称" prop="approvalName" />
                    <el-table-column align="center" label="案件流程" prop="caseProcess" show-overflow-tooltip>
                        <template slot-scope="scope">
                            {{scope.row.caseProcess | caseProcess}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="是否生效" prop="isEffective" width="120" >
                        <template slot-scope="scope">
                            {{scope.row.isEffective | isEffective}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" label="生效时间" prop="effectiveTime" width="150">
                        <template slot-scope="scope">
                            {{scope.row.effectiveTime | formatDate}}
                        </template>
                    </el-table-column>
                
                    <el-table-column align="center" label="操作" width="130">
                        <template slot-scope="scope">

                            <el-tooltip class="item" content="查看审核流程" effect="dark" placement="top-end">
                                <el-link @click.stop="seeFunction(scope.row)" class="el-icon-search btn-link" type="success"><i></i></el-link>
                            </el-tooltip>
                            <el-tooltip class="item" content="修改" effect="dark" placement="top-end" v-if="scope.row.isEffective==2">
                                <el-link @click.stop="editFun(scope.row)" class="el-icon-edit btn-link" type="primary"><i></i></el-link>
                            </el-tooltip>
                        
                        </template>
                    </el-table-column>
                </el-table>
                <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="initPage" class="page-css"/>
            </div>

            <!-- 审批流程 -->
            <step-progress :show.sync='progress.show' :title='progress.title'  :editId="progress.editId" @historyInitPage="initPage" :see="progress.see" :change="progress.change" :isSelect="progress.isSelect"></step-progress>
            <span class="dialog-footer" slot="footer">
                
               
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import LjPagination from '../../../../components/LjPagination'; 
    import {apiOrgSearch,} from "../../../../api/system";
    import {apiCaseApprovalHistory} from "../../../../api/case";
    import {getTree} from "../../../../until/menuList";
    import {formatDate,isEffective} from "../../../../until/wordBook";
    import StepProgress from "./progress";
    

    let that;
    export default {
        name: 'see',
        components:{LjPagination,StepProgress},
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: ''
            },
           editId:{
                type: String,
                default: ''
           },
           caseProcessList:{
               type:Array,
               default:[]
           }
        },
        data() {
            return {
                form: {
                    pageSize: 10,
                    pageNum: 1,
                },
                progress:{
                    show:false,
                    title:'',
                    dataid:'',
                    change:null,
                    see:false
                },
                seeObj:{
                    show:false,
                    editId:null,
                    title:''
                },
                total: 0,
                list:[]
            }
        },
        methods: {
            //关闭弹窗
            closeDialog() {
                this.$emit('update:show', false)
            },

            //修改
            editFun(row){
                this.progress.editId = row.mainId;
                this.progress.show=true;
                this.progress.title='修改审批流程';
                this.progress.change = 1;
                this.progress.see = false;
                this.progress.isSelect = true;
                
            },

            //查看
            seeFunction(row) {
                this.progress.show = true;
                this.progress.title = '查看审批流程';
                this.progress.editId = row.mainId;
                this.progress.see = true;
            },

           //初始数据
           initPage(){
               this.form.mainCode = this.editId;
              apiCaseApprovalHistory(this.form).then(res => {
                 this.list = res.data.resMap.data;
                 this.total = res.data.resMap.total
              })
           }

        },
        beforeCreate () {
            that = this;
        },
        filters:{
            formatDate(value) {
                return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
            },
            isEffective(val){
                return isEffective[val]
            },
            caseProcess(val){
                if (
                    that.caseProcessList.find(i => {
                        return i.dictValue == val;
                    })
                ) {
                    return that.caseProcessList.find(i => {
                        return i.dictValue == val;
                    }).dictName;
                }
            }

        },
      watch:{
        show(val){
            this.initPage();
        },
       
    }
    }
</script>
<style lang='less' scoped>
    .wrop {
        min-height: 300px;
       .btn-link {
        margin: 0 10px
      }
    }
</style>


