/**
* 配置->审核列表
* @module 案件
* @desc 审核列表
* @author 乔亚军
* @date 2019-6-26
**/
<template>
    <div>
        <div class="wrop-search">
            <lj-search-from :searchObj="searchObj" @searchList="searchList" :onAdd="onAdd"/>
        </div>
        <el-table :data="list" :header-cell-style="{background:'#e9eff8'}" :row-style="{cursor:'pointer'}"
                   border size="mini" v-loading='loading'>
            <el-table-column align="center" label="审批流程名称" prop="approvalName" />
            <el-table-column align="center" label="案件流程" prop="caseProcess" show-overflow-tooltip>
                <template slot-scope="scope">
                    {{scope.row.caseProcess | caseProcess}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="是否生效" prop="isEffective" width="120">
                <template slot-scope="scope">
                    {{scope.row.isEffective | isEffective}}
                </template>
            </el-table-column>
            <el-table-column align="center" label="生效时间" prop="effectiveTime" width="150">
                 <template slot-scope="scope">
                    {{scope.row.effectiveTime | formatDate}}
                </template>
            </el-table-column>
           
            <el-table-column align="center" label="操作" width="130">
                <template slot-scope="scope">
                    <el-tooltip class="item" content="查看历史版本" effect="dark" placement="top-end">
                        <el-link @click.stop="seeFunction(scope.row)" class="el-icon-search btn-link" type="success"><i></i></el-link>
                    </el-tooltip>
                    <el-tooltip class="item" content="修订" effect="dark" placement="top-end">
                        <el-link @click.stop="editFun(scope.row)" class="el-icon-edit btn-link" type="primary"><i></i></el-link>
                    </el-tooltip>
                   
                </template>
            </el-table-column>
        </el-table>
        <lj-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total"
                       @getData="getAccountList" class="page-css"/>
        <!-- 审批流程 -->
        <step-progress :show.sync='progress.show' :title='progress.title' @getAccountList="getAccountList" :editId="progress.editId" :isSelect="progress.isSelect"></step-progress>
        <!-- 历史版本查看 -->
        <history-data :show.sync='historyObj.show' :title='historyObj.title'  :editId="historyObj.editId" :caseProcessList="caseProcessList"></history-data>
    </div>
</template>
<script>
import StepProgress from "./components/progress";
import HistoryData from "./components/seeHistory";
import LjSearchFrom from '../../../components/LjSearchForm';
import LjPagination from '../../../components/LjPagination';
import {apiCaseApprovalProcessSearch} from '../../../api/case';
import {formatDate} from "../../../until/wordBook";
let that;
export default {
    name:"Approval",
    data(){
        return {
            //审批流程
            loading:true,
            progress:{
               show:false,
               title:'',
               dataid:'',
               isSelect:false
            },
            historyObj:{
               show:false,
               editId:null,
               title:''
            },
            activeName: '0',
            caseProcessList:[],
            list:[

            ],
            searchObj:[
                    {
                        type: "select",
                        name: "caseProcess",
                        options: [
                            {value: "normal", label: "一般案件"},
                            {value: "simple", label: "简易案件"}
                        ],
                        label: "审批流程"
                    }

            ],
            form: {
                pageSize: 20,
                pageNum: 1,
            },
            total: 0,
        }
    },
    components:{
        StepProgress,LjSearchFrom,LjPagination,HistoryData
    },
    methods:{
        //查看
        seeFunction(row) {
            this.historyObj.show = true;
            this.historyObj.title = '查看历史版本';
            this.historyObj.editId = row.mainCode;
        },
        // 添加
        onAdd(){
            this.progress.editId=null;
            this.progress.show=true;
            this.progress.title='添加审批流程';
            this.progress.isSelect = false;
        },

        //修订
        editFun(row){
           this.progress.editId = row.mainId;
           this.progress.show=true;
           this.progress.title='修订审批流程';
           this.progress.isSelect = true;

        },

         //查询+搜索
        getAccountList() {
            apiCaseApprovalProcessSearch(this.form).then(res => {
                this.total = res.total;
                this.list = res.data;
                this.loading = false;
            })
        },

        //搜索
        searchList(data) {  //data就是接收的this.form数据
             console.log(data)
             this.loading = true;
             data.pageSize = this.form.pageSize;
             this.form = JSON.parse(JSON.stringify(data));
             this.form.pageNum = 1;
            apiCaseApprovalProcessSearch(this.form).then(res => {
                this.list = res.data;
                this.total = res.total;
                this.loading = false;
            })

        },
    },
    beforeCreate () {
        that = this;
    },
    filters:{
        formatDate(value) {
            return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
        },
        isEffective(val){
            return val==1?'是':(val==0?'否':'')
        },
        caseProcess(val){
            if (
                that.caseProcessList.find(i => {
                    return i.dictValue == val;
                })
            ) {
                return that.caseProcessList.find(i => {
                    return i.dictValue == val;
                }).dictName;
            }
        }

    },
    created(){
        this.getAccountList();
         //审批步骤
        this.$until.workBookApi('caseProcess').then(res => {
            this.caseProcessList = res;
            let arr = [];
            res.forEach(item => {
                let obj={
                    value:item.dictValue,
                    label:item.dictName
                };
                arr.push(obj);
            });
            this.searchObj[0].options = arr;
        });
    }
}
</script>
<style lang="less" scoped>
    .wrop-search {
        margin-bottom: 10px
    }
    .btn-link {
        margin: 0 10px
    }

    .page-css {
        margin-top: 10px
    }
</style>


