/**
* 配置->审批流程
* @module 配置中心
* @desc 审批过程
* @author 乔亚军
* @date 2019-6-26
**/
<template>
    <div>
       <el-dialog
        :title="title"
        :visible="show"
        center
        width="750px"
        @close="closeDialog"
        append-to-body
        top="30px"
        >
            <div class="wrop" v-loading='loading'>
                <div>
                     <el-form
                            :model="form"
                            :rules="rules"
                            label-width="130px"
                            ref="ruleForm"
                            size="medium"
                        >
                            <el-row>
                                <el-col :span="12">
                                    <el-form-item label="案件流程：" prop="caseProcess">
                                        <el-select v-model="form.caseProcess" placeholder="请选择案件流程"  class="marginleft divwidth" :disabled='isSelect' v-if="!see">
                                            <el-option
                                                v-for="item in caseProcessList"
                                                :key="item.dictValue"
                                                :label="item.dictName"
                                                :value="item.dictValue"
                                                >
                                            </el-option>
                                        </el-select>  
                                        <span v-if="caseProcessList.find(item=>{return item.dictValue == form.caseProcess})&&see">{{caseProcessList.find(item=>{return item.dictValue == form.caseProcess}).dictName}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="审批流程名称：" prop="approvalName" >
                                        <el-input
                                                    placeholder="审批流程名称"
                                                    v-model="form.approvalName"
                                                    v-if="!see"
                                            ></el-input>
                                        <span v-if="see">{{form.approvalName}}</span>
                                    </el-form-item>
                                </el-col>
                                
                                <el-col :span="12">
                                    <el-form-item label="是否立即生效：" prop="isEffective" >
                                         <el-radio-group v-model="form.isEffective" v-if="!see">
                                            <el-radio  label="1">是</el-radio>
                                            <el-radio  label="0">否</el-radio>
                                         </el-radio-group>
                                         <span v-if="see">{{form.isEffective==1?'是':'否'}}</span>

                                    </el-form-item>

                                </el-col>
                                <el-col :span="12">
                                    <el-form-item label="生效时间：" :prop="form.isEffective==0?'effectiveTime':''" v-if="form.isEffective==0">
                                        <el-date-picker placeholder="请选择生效时间" type="date"  v-if="!see"
                                                v-model="form.effectiveTime"></el-date-picker>
                                         <span v-if="see">{{form.effectiveTime|formatDate}}</span>       
                                    </el-form-item>
                                    
                                </el-col>
                                
                                <el-col :span="24">
                                   <el-divider></el-divider>
                                </el-col>
                            </el-row> 
                        </el-form>
                </div>
                <div class="left">
                    
                    <div class="add_person">
                        
                         <!-- 发起人 -->
                         <div class="progress_wrop">
                           <div class="initiator first_wrop" >
                                <div class="sel_per first" >
                                    <el-button type="success" size="large" class="submit_btn" >发起审批</el-button>
                                </div>
                                <div class="addprogress" @click="AddProgress(-1)"  v-if="!see"><i class="el-icon-plus"></i></div>
                                <div class="addprogress" v-if="see"><i class="el-icon-bottom"></i></div>
                            </div> 
                            
                            <!-- 审批流程 -->
                            <div class="initiator" v-for="(item,i) in form.caseApprovalProcessDetailList" :key="i" >
                                <p class="head">{{item.appTitle}}<span class="span_wrop" @click="deleteFun(i)" v-if="!see"><i class="el-icon-close el-icon--right"></i></span></p>
                                <div class="sel_per" @click="progressChangeFun(i)">
                                    <p v-if="item.appSourceType==3">审批人来源类型：部门
                                        <!-- -{{item.approvalUserName}} -->
                                    </p>
                                    <p v-if="item.appSourceType==1">审批人来源类型：角色
                                        <!-- -{{approvalRoleList.find(item_2=>{return item_2.dictValue == item.approvalRoleId}).dictName}} -->
                                    </p>
                                    <p v-if="item.appSourceType==2">审批人来源类型：API</p>
                                    <i class="el-icon-view el-icon-arrow-right"></i>
                                </div>
                                <div class="addprogress" @click="AddProgress(i)"  v-if="!see"><i class="el-icon-plus"></i></div>
                                <div class="addprogress"  v-if="see"><i class="el-icon-bottom"></i></div>
                            </div>  
                            <div class="initiator submit" >
                                <el-button  type="primary" size="large" class="submit_btn" v-if="form.caseApprovalProcessDetailList.length&&!see" @click="submitFunc">提交</el-button>
                                <el-button  type="primary" size="large" class="submit_btn submit_btn2" v-if="see" >流程结束</el-button>
                            </div>
                         </div>
                        
                    </div>
                </div>
                 <!-- 蒙版 -->
                <div class="meng" :class="{ mengActive: isMeng}" @click.self="disappear">
                    <div v-show="isshow" class="right" :class="{ active: isActive, close: isClose }">
                       <div class="closeX" @click="disappear"><i class="el-icon-close el-icon--right"></i> </div>
                        
                        <el-form
                            :model="obj"
                            :rules="rules"
                            label-width="160px"
                            ref="ruleForm2"
                            size="medium"
                    >
                           <el-row>
                                <!-- 步骤数据 -->
                                
                                <el-col :span="24">
                                    <el-form-item label="审批步骤标题：" prop="appTitle">
                                        <el-input
                                                    placeholder="审批步骤标题"
                                                    v-model="obj.appTitle"
                                                    v-if="!see"
                                            ></el-input>
                                        <span v-if="see">{{obj.appTitle}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="审批人来源类型："  prop="appSourceType" >
                                        <el-radio-group v-model="obj.appSourceType" v-if="!see" >
                                            <el-radio  label="1">角色</el-radio>
                                            <el-radio  label="3">部门</el-radio>                
                                            <el-radio  label="2">API</el-radio>          
                                        </el-radio-group>  
                                        <span v-if="see">{{obj.appSourceType==1?'角色':(obj.appSourceType==2?'API':(obj.appSourceType==3?'部门':''))}}</span>
                                        
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="部门配置：" v-if="obj.appSourceType==3" >
                                        <el-button type="primary" size="mini" @click="SetOrg" v-if="!see">配置部门和人员</el-button>
                                        <el-button type="primary" size="mini" @click="seeSetOrg" v-if="see">查看人员配置</el-button>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="API地址：" :prop="obj.appSourceType==2?'appSourceApi':''" v-if="obj.appSourceType==2">
                                        <el-input
                                                    placeholder="请输入API地址"
                                                    v-model="obj.appSourceApi"
                                                    v-if="!see"
                                            ></el-input>
                                        <span v-if="see">{{obj.appSourceApi}}</span>
                                        
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="工作角色：" :prop="obj.appSourceType==1?'approvalRoleId':''" v-if="obj.appSourceType==1">
                                        <el-select v-model="obj.approvalRoleId" placeholder="请选择角色"  class="marginleft divwidth" v-if="!see">
                                            <el-option
                                                v-for="item in approvalRoleList"
                                                :key="item.roleName"
                                                :label="item.roleName"
                                                :value="item.roleName"
                                                >
                                            </el-option>
                                        </el-select>  
                                        <span v-if="approvalRoleList.find(item=>{return item.roleName == obj.approvalRoleId})&&see">{{approvalRoleList.find(item=>{return item.roleName == obj.approvalRoleId}).roleName}}</span>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="是否本部门："  v-if="obj.appSourceType==1">

                                        <el-radio-group v-model="obj.isDepartment" v-if="!see" >
                                            <el-radio  label="1">是</el-radio>
                                            <el-radio  label="0">其他部门</el-radio>
                                        </el-radio-group>  
                                        <span v-if="see">{{obj.isDepartment==1?'是':'其他部门'}}</span>                                        
                                    </el-form-item>
                                </el-col>
                                 <el-col :span="24">
                                    <el-form-item label="其他部门配置：" v-if="obj.isDepartment==0&&obj.appSourceType==1" >
                                        <el-button type="primary" size="mini" @click="SetOtherOrg" v-if="!see">其他部门配置</el-button>
                                        <el-button type="primary" size="mini" @click="seeOtherOrg" v-if="see">查看其他部门配置</el-button>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="下个流程是否判断：" prop="isJudgmentNext">

                                        <el-radio-group v-model="obj.isJudgmentNext" v-if="!see">
                                            <el-radio  label="1">是</el-radio>
                                            <el-radio  label="0">否</el-radio>
                                        </el-radio-group>  
                                        <span v-if="see">{{obj.isJudgmentNext==1?'是':'否'}}</span>                                        
                                    </el-form-item>
                                </el-col>
                                <el-col :span="24">
                                    <el-form-item label="审批意见：" prop="approvalOpinions">
                                        <el-input type="textarea" :rows="2" placeholder="请输入审批意见" v-model="obj.approvalOpinions" v-if="!see"></el-input>        
                                        <span v-if="see">{{obj.approvalOpinions}}</span>    
                                    </el-form-item>
                                </el-col>
                               
                                <el-col :span="24">
                                    <el-form-item label="抄送人：" prop="msgCopier">
                                        <el-radio-group v-model="obj.msgCopier" v-if="!see">
                                            <el-radio  label="1">承办人</el-radio>
                                            <el-radio  label="2">协办人</el-radio>                
                                            <el-radio  label="3">全部</el-radio>          
                                        </el-radio-group> 
                                        <span v-if="see">{{obj.msgCopier==1?'承办人':(obj.msgCopier==2?'协办人':(obj.msgCopier==3?'全部':''))}}</span>
                                        
                                    </el-form-item>
                                </el-col>
                              
                            </el-row> 
                        </el-form>
                        <div class="btn_rit">
                            <el-button  @click="disappear" v-if="!see">取消</el-button>
                            <el-button type="primary" @click="SaveFun('ruleForm','ruleForm2')" v-if="!see">保存</el-button>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>
        <tree-node ref="treeNode" :show.sync='treeobj.show' :idarrlistBack="treeobj.idarrlistBack" :title='treeobj.title' @getTreeData="getTreeData" @beforeSubmit="beforeSubmit" :loginIdList='loginIdList' :isSee="treeobj.isSee"></tree-node>
        <other-tree ref="treeNode2" :idarrlistBack='otherTreeObj.idarrlistBack' :show.sync='otherTreeObj.show'  :title='otherTreeObj.title' @OtherOrgId='OtherOrgId' :isSee='otherTreeObj.isSee'></other-tree>
    </div>
</template>
<script>
 import {apiOrgSearch,} from "../../../../api/system";
 import {apiCaseApprovalProcessAdd,apiCaseRoleProcessSarch,apiCommonGetOrgUser,apiCaseApprovalGetMainIdURL,apiCaseApprovalUpdateURL,apiCaseApprovalgetByMainIdAndOrderURL,apiCaseApprovalgetByMainIdAndOrderUpdateURL} from "../../../../api/case";
 import {getTree} from "../../../../until/menuList";
 import { constants } from 'crypto';
 import treeNode from "./tree";
 import {formatDate} from "../../../../until/wordBook";
 import otherTree from './setOtherOrg';
export default {
    props:{
       show:{
           type:Boolean,
           default:true
       },
       title:{
           type:String,
           default:'审批'
       },
       editId:{
           type:String,
           default:''
       },
       see:{
           type:Boolean,
           default:false
       },
      change:{
           default:''
      },
      isSelect:{
          type:Boolean,
          default:false
      }
    },
    components:{
        treeNode,otherTree
    },
    data(){
        return {
            loading:true,
            eddOrEdit:null,
            isSel:false,
            index:0,
            loginList:[],
            idarrlist:[],
            loginIdList:[],
            treeobj:{
               show:false,
               title:"",
               list:null,
               isSee:false,
               idarrlistBack:[]
            },
            otherTreeObj:{
               show:false,
               title:"",
               list:null,
               isSee:false,
               idarrlistBack:[]
            },
            mainIdObj:{
                listArr:null
            },
            expandKeys: [],
            orgTreeData:[],
            list:[{aa:'lsss'},{aa:'lsss'},{aa:'lsss'}],
            object:{},
            treeList: [],
            obj:{},
             dialogVisible: false,
             isshow:false,
             isActive:false,
             isMeng:false,
             isClose:false,
             progressList:[
                 {label:'发起人',name:'张三'},
             ],
             collarOrgList:[],
             caseProcessList:[],//案件流程
             caseSourceList: [],
             approvalRoleList:[],
             approvalOrgId:null,
             userList:[],
             userList2:[],
             appSourceTypeList:[{value:1,label:'角色'},{value:2,label:'API'},{value:3,label:'部门'}],
             msgCopierList:[{value:1,label:'承办人'},{value:2,label:'协办人'},{value:3,label:'承办人&协办人'}],
             caseSource:[],
             activeNames: ['1'],
             form:{
                caseProcess:null,
                approvalName:'',
                isEffective:'1',
                effectiveTime:null,
                caseApprovalProcessDetailList:[],//list集合
             },
            //表单验证规则
            rules: {
                appTitle: [{required: true, message: "审批步骤标题", trigger: "blur"}],
                approvalName: [{required: true, message: "审批流程名称", trigger: "blur"}],
                caseProcess: [{required: true, message: "案件流程", trigger: "blur"}],
                appSourceType: [{required: true, message: "审批人来源类型", trigger: "change"}],
                appSourceApi: [{required: true, message: "url路径地址", trigger: "blur"}],
                isJudgmentNext: [{required: true, message: "下个流程是否判断", trigger: "change"}],
                workOrgan: [{required: true, message: "请选择部门", trigger: "change"}],
                approvalRoleId: [{required: true, message: "请选择角色", trigger: "change"}],
                approvalOpinions: [{required: true, message: "请输入审批意见", trigger: "blur"}],
                msgCopier: [{required: true, message: "抄送人", trigger: "blur"}],
                otherContent: [{required: true, message: "备注信息", trigger: "blur"}],
                isEffective: [{required: true, message: "是否生效", trigger: "change"}],
                effectiveTime: [{required: true, message: "生效时间", trigger: "change"}],
                 approvalOrder: [{required: true, message: "审批次序", trigger: "change"}],
            }
        }
    },
    methods:{
        closeDialog() {
            this.$emit('update:show', false)
        },
        handleChange(data) {
            this.form.caseSource = data[data.length - 1];
        },
        
        showDetail(data) {
            this.detailId = data.orgId
        },
        //选择的tree数据
        getTreeData(data){
           this.orgTreeData = data;
        },
      
        beforeSubmit(data){
           this.idarrlist = data;
        },

        // 删除
        deleteFun(index){
            this.form.caseApprovalProcessDetailList.splice(index,1)           
        },

        //部门配置
        SetOrg(){
            this.treeobj.show=true;
            this.treeobj.title='选择组织和人员';
            this.treeobj.editId = null;
            // this.treeobj.idarrlistBack = this.idarrlist
        },
        seeSetOrg(){
            this.treeobj.show=true;
            this.treeobj.title='组织和人员查看';
            this.treeobj.editId = null;
            this.treeobj.isSee = true;
        },
        //get其他部门tree id
        OtherOrgId(data){
        //    console.log(data);
           this.approvalOrgId = data;
           this.obj.approvalOrgId=this.approvalOrgId
        },

        //SetOtherOrg
        SetOtherOrg(){
            this.otherTreeObj.show=true;
            this.otherTreeObj.title='选择其他部门';
        },
        seeOtherOrg(){
            this.otherTreeObj.show=true;
            this.otherTreeObj.title='查看其他部门';
            this.otherTreeObj.isSee = true;
        },
        
        //弹出消失
        disappear(){
           this.isMeng=!this.isMeng;
           this.isActive=!this.isActive;
           this.isClose = true;
           setTimeout(()=>{
               this.isshow = !this.isshow;
           },400);
        },
        // 修改
        progressChangeFun(index){
            this.index = index;
            this.eddOrEdit=2;
            this.obj = this.form.caseApprovalProcessDetailList[index];
            if(this.obj.idarrlist){
               this.treeobj.idarrlistBack = this.obj.idarrlist;
            }else{
                this.treeobj.idarrlistBack=[]
            }
            //  console.log(this.obj)
             //其他部门数据tree回显
             if(this.obj.approvalOrgId){
                 this.otherTreeObj.idarrlistBack = this.obj.approvalOrgId.split(',');
             }else{
                 this.otherTreeObj.idarrlistBack=[]
             }
            //    console.log(this.otherTreeObj.idarrlistBack)

            let orgListdata = this.form.caseApprovalProcessDetailList[index].orgUserList2;
            let preArr = [];
            let otherIdArr = [];
            if(orgListdata){
                 orgListdata.forEach(item=>{
                   preArr.push(item.approvalUserId);
                });
            }
            this.loginIdList = preArr;//tree回显人员id集合
            //清除验证红框
            this.$nextTick(() => {
                this.$refs.ruleForm.clearValidate();
            })
           this.isshow=!this.isshow;
           this.isActive=!this.isActive;
           this.isMeng = !this.isMeng;
           this.isClose = false;
        },
       
        //添加
        AddProgress(index){
            this.index = index;
            this.treeobj.idarrlistBack=[]
            this.otherTreeObj.idarrlistBack=[]
            this.eddOrEdit=1;
            this.obj = {
                appTitle:'',
                appSourceType:'',
                appSourceApi:'',
                workOrgan:'',
                orgUserList:this.orgTreeData,//部门人员
                approvalRoleId:null,
                isJudgmentNext:'0',
                isDepartment:'1',
                approvalOpinions:'拟同意 | 不同意',
                msgCopier:'',
                otherContent:'',
                approvalOrder:null,
                approvalOrgName:'',
                approvalUserName:'',
            };
           this.loginIdList=[];
           this.treeobj.idarrlistBack=[]
             //清除验证红框
            this.$nextTick(() => {
                this.$refs.ruleForm2.clearValidate();
            })
           this.isshow=!this.isshow;
           this.isActive=!this.isActive;
           this.isMeng = !this.isMeng;
           this.isClose = false;
        },

        //提交
        submitFunc(){
           
                    this.form.caseApprovalProcessDetailList.forEach((item,i)=>{
                        item.approvalOrder = i+1;
                    });
                    //  console.log(this.form)

                    if(!this.editId){
                        //添加
                         apiCaseApprovalProcessAdd(this.form).then(res => {
                                this.$emit("getAccountList");
                                this.$message({
                                    type: 'success',
                                    message: '提交成功!'
                                });
                                this.closeDialog();
                         })
                    }else{
                         //修订
                          apiCaseApprovalUpdateURL(this.form).then(res => {
                                this.$emit("getAccountList");
                                this.$message({
                                    type: 'success',
                                    message: '修订成功!'
                                });
                                this.closeDialog();
                         })
                    };

                     //修改
                    if(this.change){
                        this.form.mainId = this.editId;
                        apiCaseApprovalgetByMainIdAndOrderUpdateURL(this.form).then(res => {
                            this.$emit("historyInitPage");
                                this.$message({
                                    type: 'success',
                                    message: '修改成功!'
                                });
                            this.closeDialog();

                        })
                    }
                   
        },

         //保存关闭
        SaveFun(formName,formName2) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                     
                } else {
                    this.$message({
                        type: 'warning',
                        message: '请填写完整 !'
                    });
                    return false;
                }
            });
            this.$refs[formName2].validate((valid) => {
                   if(this.obj.appSourceType==1){//角色
                       this.obj.appSourceApi=null;
                       this.obj.approvalUserId=null;
                    };
                    if(this.obj.appSourceType==2){//api
                       this.obj.approvalRoleId=null;
                       this.obj.orgUserList=null;
                    };
                    if(this.obj.appSourceType==3){//部门
                       this.obj.appSourceApi=null;
                       this.obj.approvalUserId=null;
                       
                    };
                if (valid) {
                   
                  //保存到list数组里
                    if(this.eddOrEdit==1){
                       //添加保存
                       if(this.form.effectiveTime){
                          this.form.effectiveTime = Date.parse(this.form.effectiveTime);
                       }
                        this.obj.orgUserList=this.orgTreeData;
                        this.obj.idarrlist = this.idarrlist;
                        this.form.caseApprovalProcessDetailList.splice(this.index+1,0,this.obj)

                    }else if(this.eddOrEdit==2){
                      //修订保存
                        if(this.form.effectiveTime){
                            this.form.effectiveTime = Date.parse(this.form.effectiveTime);
                        }
                        if(this.obj.orgUserList.length){
                            this.obj.orgUserList=this.orgTreeData;
                        }else{
                        }
                        this.form.caseApprovalProcessDetailList[this.index]=this.obj;
                    }
                    this.disappear()
                } else {
                    this.$message({
                        type: 'warning',
                        message: '请填写完整 !'
                    });
                    return false;
                }
            });
        },
         getChangeData(loginList){
                let selectData = loginList;
                let orgUserList=[];
                selectData.forEach((item,index)=>{
                    // debugger
                    if(!item.del){
                        let arr = {
                            approvalOrgId:item.approvalOrgId,
                            approvalUserName:[item.approvalUserId+'_'+item.approvalUserName]
                        };
                        item.del = true;
                        for (let i = index + 1;i < selectData.length; i++) {
                            if(selectData[i].approvalOrgId == item.approvalOrgId){
                                arr.approvalUserName.push(selectData[i].approvalUserId+'_'+selectData[i].approvalUserName)
                                selectData[i].del = true
                            }
                        }
                        arr.approvalUserName =arr.approvalUserName.join(',');

                        orgUserList.push(arr);
                        this.loginList = orgUserList;
                    }
                });
            },

        initPage(){
           
            //审批步骤
             this.$until.workBookApi('caseProcess').then(res => {
                this.caseProcessList = res;
             });
              //角色
                //查询+搜索
                apiCaseRoleProcessSarch({pageSize: 20,pageNum: 1}).then(res => {
                    this.approvalRoleList = res.data;
                })

             
             //获取修改数据
             if(this.editId){
                 //修订数据获取
                 let idArr=[];
                 apiCaseApprovalGetMainIdURL(this.editId).then(res => {
                        let data = res.data.resMap.data;
                        data.isEffective = String(data.isEffective);
                        data.caseApprovalProcessDetailList.forEach(item => {
                            item.appSourceType = String(item.appSourceType);
                            item.isJudgmentNext = String(item.isJudgmentNext);
                            item.isDepartment = String(item.isDepartment);
                            item.msgCopier = String(item.msgCopier);
                            item.orgUserList2 = item.orgUserList;
                            this.getChangeData(item.orgUserList);
                            item.orgUserList= this.loginList;
                        });
                        this.form=data;
                        this.loading=false;
                 })
             }else{
                 //添加
                 this.form={
                    caseProcess:null,
                    approvalName:'',
                    isEffective:'1',
                    effectiveTime:null,
                    caseApprovalProcessDetailList:[],//list集合
                 };
                this.loading=false;

             };
            if(this.isSelect){
                 this.isSel = false;
            }
            
        }
    },
    filters:{
       formatDate(value){
            return formatDate(value, 'yyyy-MM-dd hh:mm:ss')
       }
    },
    watch:{
        show(val){
            this.initPage();
            this.isActive = false;
            this.isshow = false;
        },
       
        index(val){

        },

    }

}
</script>
