/**
* 配置->审核tree
* @module 配置中心
* @desc 部门审批树形组件
* @author 乔亚军
* @date 2019-6-26
**/
<template>
    <div>
        <el-dialog :close-on-click-modal='false' :title="title" :visible="show" @close="closeDialog" center append-to-body
                   width="650px" top="30px">
            <div class="wrop">
                <el-tree
                    v-if="show"
                    :data="treeList"
                    show-checkbox
                    node-key="orgId"
                    :props="{label:'orgName'}"
                    @node-expand='dropNode'
                    accordion
                    :default-checked-keys="defaultArr"
                    :default-expanded-keys="expandedArr"
                    ref="tree"
                    >
                </el-tree>
            </div>
            <span class="dialog-footer" slot="footer">
                
                <el-button @click="treeSelect()" size="small" type="primary" v-if="!isSee"><i
                        class="iconfont icon-save"></i>确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiOrgSearch,} from "../../../../api/system";
    import {apiCommonGetOrgUser,apiCaseApprovalgetByMainIdAndOrderURL} from "../../../../api/case";
    import {getTree} from "../../../../until/menuList";
    import md5 from 'js-md5'
import { debug } from 'util';

    let that;
    export default {
        name: 'see',
        props: {
            show: {
                type: Boolean,
                default: false
            },
            title: {
                type: String,
                default: '选择'
            },
            loginIdList: {
                type: Array,
                default: null
            },
            isSee:{
                type:Boolean,
                default:false
            },
            idarrlistBack:{
                default:null
            }

        },
        data() {
            return {
                treeList:[],
                defaultArr:[],
                expandedArr:[],
                selectData:null
            }
        },
        methods: {
            //关闭弹窗
            closeDialog() {
                this.$emit('update:show', false)
            },

           dropNode(data){
            //   console.log(data)
           },

           
            //树状选择时触发
            treeSelect(){
                let selectData = this.$refs.tree.getCheckedNodes(true);
                let idarrlist = []
                selectData.forEach(item=>{
                   idarrlist.push(item.orgId)
                });
                this.$emit("beforeSubmit",idarrlist);
                
                let orgUserList=[];
                let arrData = JSON.parse(JSON.stringify(selectData))
                arrData.forEach((item,index)=>{
                    // debugger
                    if(!item.del){
                        let arr = {
                            approvalOrgId:item.b,
                            approvalUserName:[item.loginId+'_'+item.orgName]}
                        item.del = true;
                        for (let i = index + 1;i < arrData.length; i++) {
                            if(arrData[i].b == item.b){
                                arr.approvalUserName.push(arrData[i].loginId+'_'+arrData[i].orgName)
                                arrData[i].del = true
                            }
                        }
                        arr.approvalUserName =arr.approvalUserName.join(',')
                        orgUserList.push(arr)
                    }
                });
                this.selectData = orgUserList;
                this.$emit("getTreeData",this.selectData);
                this.closeDialog()

            },
           initPage(){
            //组织部门
                apiOrgSearch().then(res => {
                    res.data.forEach(i => {
                        i.name = i.orgName
                    });
                this.treeList = getTree(res.data, 'orgId', 'parentCode', 'isParent');
                this.treeList.forEach(item=>{
                        item.children.forEach(item2=>{
                            item2.children.forEach(item3=>{
                                apiCommonGetOrgUser(item3.orgId).then(res => {
                                    res.userList.forEach(i=>{
                                        i.a=i.orgName;
                                        i.b =i.orgId;
                                        i.orgName=i.fullName;
                                        i.orgId =i.loginId;
                                        
                                        // this.$refs.tree.setCheckedKeys([i.loginId],true);
                                    });
                                    item3.children= res.userList;
                                    
                                });
                            });
                        });
                    });
                });
            
           }

        },
       
        created(){
            this.initPage();
        },
      watch:{
        show(val){
            if(val){
                this.expandedArr=["06414fb27d1e400995f85c14fa15b091"]
                this.defaultArr=[]
                if(this.loginIdList.length){
                   this.defaultArr = this.loginIdList;
                };
                if(this.idarrlistBack.length){
                   this.defaultArr = this.idarrlistBack;
                }
            }
        },
       
    }
    }
</script>
<style lang='less' scoped>
    .wrop {
        // min-height: 500px;
       
    }
</style>


